import styled from "styled-components";

export const StyledMenu = styled.nav`
  flex-direction: column;
  justify-content: center;
  background: white;
  transform: ${({ open }) => (open ? "translateX(0)" : "translateX(-100%)")};
  height: 100vh;
  text-align: left;
  padding: 2rem;
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.3s ease-in-out;
  z-index: 9;
  overflow-y: auto;

  ul {
    list-style: none;
  }

  @media (max-width: 576px) {
    width: 100%;
  }
  .rmd-list-item__text {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    padding-left: 0.7rem;
    padding-top: 0.5rem;
  }

  img {
    width: 3rem;
    height: 3rem;
    display: block;
    border-radius: 125px;
    -webkit-border-radius: 125px;
    -moz-border-radius: 125px;
  }
  li {
    display: flex;
    padding-bottom: 0.85rem;
  }
  a {
    font-size: 1.5rem;
    padding: 1.5rem 0;
    font-weight: bold;
    letter-spacing: 0.2rem;
    color: black;
    text-decoration: none;
    transition: color 0.3s linear;

    @media (max-width: 576px) {
      font-size: 1.5rem;
      text-align: center;
    }

    &:hover {
      opacity: 0.5;
      color: grey;
    }
  }
`;
