import styled from "styled-components";

export const StyledLogo = styled.figure`
  position: absolute;
  margin: 0%;
  top: 0%;
  right: 4%;
  left: 4%;
  height: 50px;
  text-align: right;
  border-radius: 14px;
  background: rgba(255, 255, 255, 1);
  width: 92%;
  z-index: 8;
  img {
    padding-top: 6px;
    padding-bottom: 6px;
    padding-right: 5%;
    max-height: 76%;
    max-width: 40%;
    object-fit: contain;
  }
`;
