import React from "react";
import { StyledLogo } from "./Logo.styled";

const Logo = ({ noHayImagen, rutaImagen, ...props }) => {
  function devolverLogo() {
    if (!noHayImagen) {
      return (
        <StyledLogo {...props}>
          <img src={rutaImagen} alt="logo" />
        </StyledLogo>
      );
    } else {
      return null;
    }
  }

  return devolverLogo();
};

export default Logo;
