import React from "react";
import { bool } from "prop-types";
import { StyledMenu } from "./Menu.styled";
import { Avatar } from "@react-md/avatar";
import { List, ListItem, ListSubheader } from "@react-md/list";

const Menu = ({ open, lista, ...props }) => {
  const isHidden = open ? true : false;

  function GetImagen(imagen) {
    return <Avatar src={imagen.imagen}></Avatar>;
  }

  function control() {
    if (lista !== undefined) {
      return (
        <StyledMenu open={open} aria-hidden={!isHidden} {...props}>
          <List>
            <ListSubheader></ListSubheader>
            {lista.map((list, i) => (
              <a href={list.goTo}>
                <ListItem
                  className="item"
                  id="two-line-item-0"
                  leftAddon={<GetImagen imagen={list.image} />}
                  leftAddonType="avatar"
                >
                  {list.title}
                </ListItem>
              </a>
            ))}
          </List>
        </StyledMenu>
      );
    } else {
      return (
        <StyledMenu open={open} aria-hidden={!isHidden} {...props}>
          <List>
            <ListSubheader></ListSubheader>
          </List>
        </StyledMenu>
      );
    }
  }
  return control();
};

Menu.propTypes = {
  open: bool.isRequired
};

export default Menu;
